import './index.scss';

import { axios } from '@/utils/axios';

import { Button, Table, Divider, Modal, message } from 'antd';
import UpdateType from './updateType';

export default class TypeAdmin extends Component {
	state = {
		data: [],
		myvisible: 0,
		loading: false,
		type: {}
	};
	showModal = () => {
		this.setState({
			myvisible: 1,
			type: {}
		});
	};
	handleOk = () => {
		const { myvisible,type } = this.state;
		this.form.validateFields(async (err, values) => {
			let categoryName = values.type;
			console.log(categoryName);
			console.log(type);
			if (!err) {
				let result;
				if (myvisible == 1) {
					result = await axios.get('/manage/category/add', { params: { categoryName } });
				}
				if (myvisible == 2) {
				  result = await axios.post("/manage/category/update",{categoryName,categoryId:type._id})
				}

				const action = myvisible == 1 ? '添加' : '修改';
				this.setState({
					myvisible: 0,
					type:{}
				});
				if (result.data.status == 0) {
					message.success(action + '分类成功');
					this.getData();
				} else {
					message.error(action + '分类失败');
				}
			}
			console.log(type)
		});
	};

	handleCancel = () => {
		this.setState({
			myvisible: 0,
			type: {}
		});
	};

	getData = async () => {
		this.setState({
			loading: true
		});
		const result = await axios.get('/manage/category/list');
		this.setState({
			data: result.data.data
		});
		this.setState({ loading: false });
	};

	componentWillMount(){
		this.setState({
			type:{}
		})
	}
	componentDidMount() {
		this.getData();
	}
	render() {
		const columns = [
			{
				title: '分类名称',
				dataIndex: 'name',
				key: 'name',
				width: '80%'
			},
			{
				title: '操作',
				key: 'action',
				width: '20%',
				render: (type) => (
					<span>
						<a
							onClick={() => {
								this.setState({ myvisible: 2 });
								this.setState({
									type
								});
							}}
						>
							修改分类
						</a>
					</span>
				)
			}
		];
		const { myvisible, data, loading, type } = this.state;
		
		return (
			<div className="typeAdmin">
				<div className="content">
					<div className="contentTop">
						<Button type="primary" onClick={this.showModal}>
							+ 添加
						</Button>
						<Modal
							title={myvisible == 1 ? '添加分类' : '修改分类'}
							visible={myvisible !== 0}
							onOk={this.handleOk}
							onCancel={this.handleCancel}
						>
							<UpdateType setForm={(form) => (this.form = form)} typeName={type.name} />
						</Modal>
					</div>
					<div className="contentTable">
						<Table
							columns={columns}
							loading={loading}
							bordered
							dataSource={data}
							pagination={{ defaultPageSize: 6, showQuickJumper: true }}
							rowKey="_id"
						/>
					</div>
				</div>
			</div>
		);
	}
}
